<template>
  <form class="addtodo" @submit="addTodo">
    <input type="text" v-model="title" name="title" placeholder="Add Todo.." />
    <input type="submit" value="Submit" class="btn" />
  </form>
</template>

<script>
export default {
  name: "AddTodo",
  data() {
    return {
      title: ""
    };
  },
  methods: {
    addTodo(e) {
      e.preventDefault();
      const newTodo = {
        id: 4,
        title: this.title,
        completed: false
      };
      this.$emit("add-todo", newTodo);
      this.title = "";
    }
  }
};
</script>
<style scoped>
form {
  display: flex;
}

input[type="text"] {
  flex: 6;
  padding: 5px;
}

input[type="submit"] {
  flex: 6;
}
</style>
